document.addEventListener('DOMContentLoaded', function() {
  iniciarApp()
})

function iniciarApp() {
  navegacionFija()
  createGallery()
  scrollNav()
}

function navegacionFija() {
  const barra = document.querySelector('.header')
  const aboutFestival = document.querySelector('.about-festival')
  const body = document.querySelector('body')

  window.addEventListener('scroll', function() {
      if(aboutFestival.getBoundingClientRect().top < 0) {
      barra.classList.add('fijo')
      body.classList.add('body-scroll')
    } else {
      barra.classList.remove('fijo')
      body.classList.remove('body-scroll')
    }
  })
}


function scrollNav() {
  const enlaces = document.querySelectorAll('.main-navigation')

  enlaces.forEach( enlace => {
    enlace.addEventListener('click', function(e) {
      e.preventDefault()

      const scrollSection = e.target.attributes.href.value
      const section = document.querySelector(scrollSection)
      section.scrollIntoView({behavior: "smooth"})
    })

  })
}

function createGallery() {
  const gallery = document.querySelector('.images-gallery');
  
  for (let i = 1; i <= 12; i++) {
    const image = document.createElement('picture')
    image.innerHTML = `
      <source srcset="build/img/thumb/${i}.avif" type="image/avif">
      <source srcset="build/img/thumb/${i}.webp" type="image/webp">
      <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
    `
    image.onclick = function() {
      showPicture(i)
    }
    
    gallery.appendChild(image)
  } 
}

function showPicture(id) {
  const image = document.createElement('picture')
  image.innerHTML = `
    <source srcset="build/img/grande/${id}.avif" type="image/avif">
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria">
  `

  // Crea el overlay con la imagen
  const overlay = document.createElement('DIV')
  overlay.appendChild(image)
  overlay.classList.add('overlay')
  overlay.onclick = function() {
    const body = document.querySelector('body')
    body.classList.remove('fijar-body')
    overlay.remove()
  }

  // Boton para cerrar el Modal
  const closeModal = document.createElement('P')
  closeModal.textContent = 'X'
  closeModal.classList.add('btn-close')
  closeModal.onclick = function() {
    const body = document.querySelector('body')
    body.classList.remove('fijar-body')
    overlay.remove()
  }
  overlay.appendChild(closeModal)

  //  AÃ±adir al HTML 
  const body = document.querySelector('body')
  body.appendChild(overlay)
  body.classList.add('fijar-body')
}